<template>
  <div class='album'>
     <header>
        <router-link to='/radio'>
         <img src='./img/top-icon-01@2x.png'/>
        </router-link>
       <span>专辑</span>
       <img src='./img/top-icon-02@2x.png'/>  
     </header>  
     <div class='zj'>
         <div class='right'>
            <img src='./img/picture-02@2x.png' /> 
             <img src='./img/picture-01@2x.png'/> 
         </div>
         <div class='left'>
             <p>spirit-stallion <br> cimarron soundrack</p>
             <div>
                 <img src='./img/head-picture-01@2x.png'/>  
                 <img src='./img/head-picture-02@2x.png'/>  
             </div>
             <p class='j'>简介：原始的美国西部，一个美丽的村庄</p>
         </div>
     </div>
      <div class='love'>
             <p @click='add'>
                 <img src='./img/icon-03@2x.png' /> 
                 <span>{{num}}</span>
             </p>
             <p @click='ly'>
                 <img src='./img/icon-04@2x.png' /> 
                 <span class='dd'>...</span>
                 <span>{{num1}}</span>
             </p>
             <p>
                 <img src='./img/icon-05@2x.png'/> 
                 <span class='dd'>...</span>
             </p>
         </div>
     <main class='main'>
        <div>
            <img src='./img/icon-06@2x.png' class='img1'/> 
            <p> 全部播放<span>（{{msg.length}}首）</span></p>
            <img src='./img/icon-07@2x.png' class='img2' @click='audio'/> 
        </div>
        <ol>
            <li v-for='(item,index) in msg' :key='index' @click='bf(index)'>
                <a herf='#'>
                    <p class='text'>
                        <span :class='{ys:item.active}'>{{item.title}}</span>
                        <img src='./img/icon-09@2x.png' />  
                        <img src='./img/icon-08@2x.png' v-show='item.active'/>
                    </p>
                    <p class='dj'>
                        <span class='span'>{{item.sq}}</span>
                        <span class='span'>{{item.dj}} </span>
                        <span class='span1'>{{item.ms}}</span> 
                    </p>
                </a>  
            </li>
        </ol>
  </main>
  </div>

</template>

<script>
import Text from './../../../static/json/album';
export default {
  data() {
    return {
       msg:Text.list,
       num:2668,
       num1:23,
    }
  },
  methods: {
     bf(index){
         for(let i in this.msg){
             this.msg[i].active=false;
         }
         this.msg[index].active=true
     },
     add(){
         let n=this.num+=1;
         this.num=n;
     },
     ly(){
         
         alert('感谢亲的留言哟！');
         this.num1=this.num1+=1;
     },
     audio(){
         alert('添加成功！');
     }
  },
  components: {

  }
}
</script>

<style scoped lang='less'>
body{
    margin: auto;
}
// @import './../../../static/css/common.css';
 .album{
    //  头部
    // width:100%;
    height:100%;
   
    padding-left:.32rem;
    padding-right:.32rem;
    background:url('./img/banner.png') no-repeat center center;
    background-size:100% 101%;
        margin-bottom:-15px;
    header{
       display:flex;
       justify-content:space-between;
       padding-top:.28rem;
       img{
           width:.44rem;
           height:.44rem;
       }
       img:nth-child(1){
           position:relative;
          top:-.6rem;
       }
       span{
           font-size:.36rem;
           color:rgb(51,51,51);
       }
    }
    .zj{
         display:flex;
         height:2.5rem;
        justify-content:space-between;
        .right{
            position:relative;
            img:nth-child(1){
               width:2.5rem;
               height:2.5rem;
               position:relative;
              left:.4rem;
             animation: bounce-in .8s ease infinite;
           }
        @keyframes bounce-in {
            0% {
                transform: scale(.5);
            }
            50% {
                transform: scale(1.2);
            }
            100% {
                transform: scale(.5);
            }
            }
           img:nth-child(2){
                width:2.12rem;
               height:2.12rem;
                position:relative;
              top:-2.6rem; 
              animation: bounce-in .8s ease infinite;
           }
        }
        .left{
            font-size:.36rem;
            width:50%;
             color:rgb(51,51,51);
             margin-top:-.5rem;
            p{
                white-space:nowrap;
                text-overflow:ellipsis;
                overflow:hidden;
                animation:bounce .5s ease 1;
            }
            .j{
                font-size:.28rem;
            }
            @keyframes late {
                0%{
                   transform:translate(800px 800px);
                }
                100%{
                   transform:translate(0px 0px);
                }
            }
            p:last-child{
                 color:rgb(102,102,102);
            }
            div{
                img{
                    width:1rem;
                    height:1rem;
                }
                img:nth-child(2){
                    margin-left:-.5rem;
                   animation:bounce 3s linear infinite;
                }
                 @keyframes bounce {
                    0% {
                        transform: rotate(-50deg);
                    }
                    18% {
                        transform: rotate(-100deg);
                    }
                    35% {
                        transform: rotate(-150deg);
                    }
                    55% {
                        transform: rotate(-200deg);
                    }
                    70% {
                        transform: rotate(-250deg);
                    }
                    85% {
                        transform: rotate(-300deg);
                    }
                    100% {
                        transform: rotate(-360deg);
                    }
                    }
            }
        }
    }
    .love{
        display:flex;
        justify-content:space-between;
        margin-top:-1rem;
        p{
            position:relative;
            span{
                font-size:.2rem;
                 color:rgb(102,102,102);
                position:relative;
                top:-.1rem;
            }
            img{
                width:.44rem;
                height:.44rem;
            }
            .dd{
                font-size:.3rem;
                color:rgb(16,97,92);
                position:absolute;
                left:.08rem;
                top:.55rem;
            }

        }
    }
    .main{
        div{
            overflow:hidden;
             margin-top:-.3rem;
            .img1{
                float:left;
                width:.4rem;
                height:.4rem;
            }
            p{
                float:left;
                font-size:.32rem;
                color:rgb(51,51,51);
                margin-left:.24rem;
                margin-top:-.05rem;
            }
            span{
                color:rgb(102,102,102);    
            }
            .img2{
                float:right;
                width:.4rem;
                height:.4rem;
            }

        }
        ol{
            margin-top:-.2rem;
            li{
                height:100%;
                font-size:.32rem;
                color:rgb(16,97,92);
                margin-top:-.2rem;
              a{
                  height:100%;
                  display:block;
                
                  .text{
                      font-size:.34rem;
                      color:rgb(51,51,51);
                      .ys{
                          color:rgb(16,97,92);
                      }
                      img{
                          width:.44rem;
                          height:.44rem;
                          float:right;
                      }
                  }
                  
                  .dj{
                      margin-top:-.2rem;
                     .span{
                         display:inline-block;
                         width:.54rem;
                         height:.28rem;
                         line-height:.28rem;
                         text-align:center;
                         border:1px solid rgb(16,97,92);
                         border-radius:.10rem;
                         margin-right:.1rem;
                         font-size:.2rem;
                         color:rgb(16,97,92);
                     }
                     .span1{
                         font-size:.2rem;
                         color:rgb(102,102,102);
                         white-space:nowrap;
                        text-overflow:ellipsis;
                        overflow:hidden;
                     }
                  }
              }
            }
        }
    }

 }
</style>



